body {
    margin: 0;
    padding: 0;
    font-family: -apple-system;
    background-color: lightblue;
}

.App {
    background-color: white;
    max-width: 680px;
    margin: auto;
}

.Navbar {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    background-color: hotpink;
    box-shadow: 3px 3px 3px #333;
}

@media only screen and (max-width : 600px){
    .Navbar {
        display: grid;
        grid-template-columns: 1fr;
    }

    .nav-menu {
        display: grid;
        grid-template-columns: 1fr;
        padding:0;
    }

    .nav-menu li a{
        display: block;
        text-align: center;
        margin: 2px;
    }
}

.nav-menu{
    list-style-type: none;
    margin-top: auto;
    margin-bottom: auto;
}

.logo-link a {
    color: white;
    text-decoration: none;
    margin: 10px;
}

.logo-link a:hover {
    color: black;
    transition: all 1s ease-in-out;
}

.nav-menu li {
    display: inline-block;
}

.nav-menu li a {
    text-decoration: none;
    background-color: white;
    padding: 10px;
    border-radius: 2px;
    margin-right: 5px;
}

.nav-menu li a:hover {
    background-color: black;
    color: white;
    transition: 1s ease-in-out;
}
.title {
    text-align: center;
    background-color: black;
    color: darkgoldenrod;
    padding: 10px;
    box-shadow: 3px 3px 3px #333;
    width: 30%;
    margin-left: auto;
    margin-right: auto;
}

ul {list-style-type: none;}

.container {
    display: grid;
    grid-template-columns: 1fr;
    padding: 10px 10px 30px 10px;
}

.defaultImage {
    position: absolute;
    z-index: 0;
    top: 40%;
    left: 39.5%;
    width: 440px;
    height: 280px;
}

.smallImage {
    width: 180px;
    height: 140px;
    transition: 1s;
}

.smallImage:hover {
    -webkit-transform: scale(.9);
            transform: scale(.9);
    opacity: .8;
    transition: 1s;
}

.bigImage {
    position: absolute;
    top: 40%;
    left: 39.5%;
    width: 440px;
    height: 280px;
    display: none;
}

li:hover .bigImage{
    display: block;
    z-index: 1;
}




